import React from "react";
import classes from "./Burger.module.css";
import BurgerIngredient from "./BurgerIngredient/BurgerIngredient";

const burger = (props) => {
  // (1) Object.keys(props.ingredients) returns an Array of Strings (ingredient name/keys)
  // (2) Each of that ingredientKey is mapped...
  // (3) A two-element array is constructed with ingredientKey and it's value
  // (4) That two-element array is mapped to yield the <BurgerIngredient /> element.
  // (5) All such <BurgerIngredient /> elements are 'spread' to form transformedIngredients.

  let transformedIngredients = Object.keys(props.ingredients)
    .map((ingredientKey) => {
      return [...Array(props.ingredients[ingredientKey])].map(
        (ingName, ingVal) => {
          return (
            <BurgerIngredient
              key={ingredientKey + ingVal}
              type={ingredientKey}
            />
          );
        }
      );
    })
    .reduce((arr, el) => {
      return arr.concat(el);
    }, []);

  if (transformedIngredients.length === 0) {
    transformedIngredients = <p>Please start adding ingredients!</p>;
  }

  return (
    <div className={classes.Burger}>
      <BurgerIngredient type="bread-top" />
      {transformedIngredients}
      <BurgerIngredient type="bread-bottom" />
    </div>
  );
};

export default burger;
