import React, { Component } from "react";
import axios from "../../axios-orders";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";

import Burger from "../../components/Burger/Burger";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import OrderSummary from "../../components/Burger/OrderSummary/OrderSummary";

import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/spinner";
 
const INGREDIENT_PRICES = {
	salad: 0.5,
	cheese: 0.4,
	meat: 1.3,
	bacon: 0.7,
};

/////////////////////////////////////////////////////////////////////////////////////////
/////////////////////////////////////////////////////////////////////////////////////////

class BurgerBuilder extends Component {
	state = {
		ingredients: null,
		totalPrice: 4,
		purchasing: false,
		loading: false,
		error: null,
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	componentDidMount() {
		axios
			.get("/ingredients.json")
			.then((response) => {
				let startPrice = 4;
				for (let ing in response.data) {
					startPrice += INGREDIENT_PRICES[ing] * response.data[ing];
				}
				this.setState({ ingredients: response.data, totalPrice: startPrice });
			})
			.catch((error) => {
				this.setState({ error: true });
			});
	}

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	purchaseHandler = () => {
		this.setState({ purchasing: true });
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	purchaseCancelHandler = () => {
		this.setState({ purchasing: false });
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	purchaseContinueHandler = () => {
        
        const queryParams = [];
        for (let i in this.state.ingredients) {
            // i is the key (bacon, cheese, etc.) and value is the ingredient count (look at state to understand)
            queryParams.push(encodeURIComponent(i) + "=" + encodeURIComponent(this.state.ingredients[i]));
        }

        queryParams.push('price=' + this.state.totalPrice);

        // join each element with "&" and form one whole string
        const queryString = queryParams.join("&");

        this.props.history.push({
            pathname: "checkout",
            search: "?" + queryString
        });   
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	resetHandler = () => {
		const resetState = {
			ingredients: {
				salad: 0,
				bacon: 0,
				cheese: 0,
				meat: 0,
			},
			totalPrice: 4,
			purchasing: false,
			loading: false,
		};

		// Reset state to an empty burger
		this.setState(resetState);
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	addIngredientHandler = (type) => {
		// immutably update the ingredients list
		const oldCount = this.state.ingredients[type];
		const updatedIngredients = {
			...this.state.ingredients,
		};
		updatedIngredients[type] = oldCount + 1;

		// update the total price
		const oldPrice = this.state.totalPrice;
		const newPrice = oldPrice + INGREDIENT_PRICES[type];

		// set state for both updates
		this.setState({ totalPrice: newPrice, ingredients: updatedIngredients });
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	removeIngredientHandler = (type) => {
		const oldCount = this.state.ingredients[type];

		// make changes only if an ingredient can be removed
		if (oldCount <= 0) return;

		// immutably update the ingredients list
		const updatedIngredients = {
			...this.state.ingredients,
		};
		updatedIngredients[type] = oldCount - 1;

		// update the total price
		const oldPrice = this.state.totalPrice;
		const newPrice = oldPrice - INGREDIENT_PRICES[type];

		// set state for both updates
		this.setState({ totalPrice: newPrice, ingredients: updatedIngredients });
	};

	/////////////////////////////////////////////////////////////////////////////////////////
	/////////////////////////////////////////////////////////////////////////////////////////

	render() {
		const disabledInfo = {
			...this.state.ingredients,
		};
		for (let key in disabledInfo) {
			disabledInfo[key] = disabledInfo[key] <= 0 ? true : false;
		}

		let orderSummary;

		// Full Burger starts either as a spinner (no error but fetching ingredients) or error message
		let fullBurger = this.state.error ? (
			<h3 style={{ textAlign: "center" }}>Ingredients can't be loaded</h3>
		) : (
			<Spinner />
		);

		// If ingredient info is loaded, Burger will contain the 'Ingredient' and 'Buildcontrol' components
		if (this.state.ingredients !== null) {
			fullBurger = (
				<React.Fragment>
					<Burger ingredients={this.state.ingredients} />
					<BuildControls
						resetBurgerHandler={this.resetHandler}
						ingredientAdded={this.addIngredientHandler}
						ingredientRemoved={this.removeIngredientHandler}
						disabled={disabledInfo}
						purchasing={this.purchaseHandler}
						price={this.state.totalPrice}
					/>
				</React.Fragment>
			);

			// set up order summary
			orderSummary = <Spinner />;

			if (!this.state.loading) {
				orderSummary = (
					<OrderSummary
						totalPrice={this.state.totalPrice}
						ingredients={this.state.ingredients}
						purchaseCancelled={this.purchaseCancelHandler}
						purchaseContinued={this.purchaseContinueHandler}
					/>
				);
			}
		}

		return (
			<React.Fragment>
				<Modal
					show={this.state.purchasing}
					modalClick={this.purchaseCancelHandler}
				>
					{orderSummary}
				</Modal>

				{fullBurger}
			</React.Fragment>
		);
	}
}

export default withErrorHandler(BurgerBuilder, axios);
